@import '../../variables.scss';

.CartProduct {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 200px;
    padding: 0 30px 0 10px;

    border-bottom: 1px solid $gray-text;

    &:last-child {
        border-bottom: none;
    }
}

.order {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 5%;

    font-size: 1.4rem;
    font-weight: 700;
}

.info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-basis: 95%;
    height: 100%;
}

.picture {
    display: block;
    height: 70%;
    flex-basis: 230px;
    flex-shrink: 0;
    margin-right: 30px;
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.thumbnail {
    display: block;
    height: 100%;
    width: 100%;
}

.content {
    flex-basis: 50%;
    text-decoration: none;
    color: $black-text;
}

.title {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;

    margin-bottom: 20px;
}

.name {
    font-size: 1.8rem;
    margin-right: 20px;
}

.description {
    margin-bottom: 15px;
}

.amount {
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-width: 160px;
    margin-bottom: 10px;

    border: 1px solid $gray-text;
    border-radius: 10px;
    padding: 5px 15px;
    &Num {
        margin-right: 10px;

        font-size: 2rem;
        font-weight: 300;

        user-select: none;
    }
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    width: 25px;
    border: none;
    cursor: pointer;
    margin-right: 10px;

    &:last-child {
        margin-right: 0;
    }

    &:focus {
        outline: none;
    }
}

.icon {
    display: block;
    width: 100%;
    height: 100%;
}

.cost {
    text-align: right;
    font-size: 1.8rem;
    color: $blue-bg;

    user-select: none;
    &Text {
        font-size: 1.4rem;
    }
}

.euro {
    font-size: 1.2rem;
}
