@import '../../variables.scss';

.Product {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    height: 100%;

    padding: 50px 0;
    border-radius: 10px;

    background-color: $white;
}
.price {
    align-items: center;
}

.presentation {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-basis: 45%;
    padding: 20px 30px;
    margin-right: 30px;

    border-radius: 10px;
}
.info {
    flex-grow: 1;
    height: 100%;

    padding: 25px 30px;

    border-radius: 10px;

    &Title {
        font-size: 3.6rem;
        line-height: 1;
    }

    &Brand {
        font-size: 1.8rem;
        font-weight: 300;
    }

    &Description {
        max-width: 70%;
        margin-bottom: 20px;

        font-size: 1.4rem;
        font-weight: 300;
    }

    &Price {
        font-weight: 700;
        font-size: 2.6rem;
        color: $blue-bg;
        margin-right: 20px;
    }

    &Container {
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        margin-bottom: 100px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &Name {
        margin-bottom: 50px;
        justify-content: space-between;
    }

    &RSC {
        margin-bottom: 50px;
        &Item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 50px;

            &:last-child {
                margin-right: 0;
            }
        }

        &Icon {
            width: 20px;
            height: 20px;
            margin-right: 5px;
        }

        &Text {
            font-weight: 700;
            font-size: 1.2rem;
        }
    }
}

.fwRegular {
    font-weight: 400;
}
